<script>
	import Vue from 'vue'
	import { TabContainer, TabContainerItem } from 'mint-ui';
    import Tab from "./Tab"
	Vue.component(TabContainer.name, TabContainer);
	Vue.component(TabContainerItem.name, TabContainerItem);
	import { mapState, mapActions } from 'vuex'
	import axios from "axios"
	export default {
		mySwiper: null,
		mySwiper2: null,
		name: "detail",
		data() {
			return {
				btn1: false,
				btn2: false,
				tab: true,
				data: null,
			    addnum:1,
				id: this.$router.currentRoute.params.id

			}
		},
		computed:{ 	
  			...mapState({items:'goods',tnum:'totalnum'})
        },
		mounted() {
			var that = this;
			this.mySwiper = new Swiper('.swiper-container', {

				autoplay: 5000, //
				pagination: '.swiper-pagination',
				paginationClickable: true
			})
			this.mySwiper2 = new Swiper('.detail-koubei', {

				//pagination: '.swiper-pagination',
				slidesPerView: 1.2,
				//paginationClickable: true,
				spaceBetween: 10
			})
			this.getDetail()
			this.scrollTop()
			$(document).ready(function() {
				//
				$(".add").click(function() {
					var n = $(this).prev().val();
					var num = parseInt(n) + 1;
					if(num == 0) {
						return;
					}
					$(this).prev().val(num);
				});
				//
				$(".jian").click(function() {
					var n = $(this).next().val();
					var num = parseInt(n) - 1;
					if(num == 0) {
						return
					}
					$(this).next().val(num);
				});
			})

		},
		methods: {
			add(id,name,pic,price){
		      //
		    	 var info = {
		    	 	  id:id,
		    	 	  name:name,
		    	 	  pic:pic,
		    	 	  price:price,
		    	 	  isChecked:true,
		    	 	  num:this.addnum
		    	 }
//		    	 console.log(this.addnum)
//		    	 console.log(info)
		    	 this.$store.dispatch("addLists",info)
           },

			totop() {
				var n = $("#tab").offset().top
				//console.log(n)
				$(document).scrollTop(n)

			},
			scrollTop() {

				$(window).scroll(function() {
					var m = $(document).scrollTop();

					//var n = $(".titleBar").offset().top
					//console.log(n)
					if(m >= 888) {
						$("#tt").css("display", "block")
						$(".titleBar").css({
							"position": "fixed",
							"top": 0,
							"left": 0,
							"margin": 0
						})

					} else {
						$(".titleBar").css({
							"position": "static",
							"margin-top": 20
						})
						$("#tt").css("display", "none")

					}
				})

			},

			clear() {
				this.btn1 = false;
				this.btn2 = false;
				this.addnum = parseInt($(".num").val())
			
//				console.log(this.addnum)
			},

			getDetail() {
				var that = this;
				axios.get("https://m.mia.com/s/cat288_jh1_f0.html?per_page=40&per_data=1").then(function(res) {
					//console.log(res.data)
					for(var i = 0; i < res.data.length; i++) {
						if(that.id == res.data[i].id) {
							that.data = res.data[i]
							//console.log(that.data)

						}

					}

				})

			}

		},
		watch:{
		  	items:{//
		  		 handler:function(val){
		  		 	 this.$store.dispatch("getNum");
		  		 },
		  		 deep:true
		  	}
        }

	}
</script>

