	}
	
	footer {
		width: 100%;
		height: 46px;
		background: #fff;
		display: flex;
		align-items: center;
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 200;
		font-size: 0.35rem;
		.left,
		.right {
