			color: #5f646e;
			em{
				height: .5973*0.625rem;
			    color: #fff;
			    font-size: .4267*0.625rem;
			    border-radius: .5973rem;
			    min-width: .5973*0.625rem;
			    background: #fa4b9b;
			    text-align: center;
			    line-height: .5973*0.625rem;
			    font-style: normal
						}
			
		}
		.right {
			line-height: 46px;
			flex-grow: 1;
			text-align: center;
			background: #FF4E88;
			color: #fff;
		}
	}
</style>


