<template>
		
<div class="tab" id="tab">

   
  <span @click="addCurrent">
 <mt-navbar class="titleBar" v-model="active" >
  <mt-tab-item @click="changeDiv(1)" id="1"></mt-tab-item>
  <mt-tab-item @click="changeDiv(2)" id="2"></mt-tab-item>
  <mt-tab-item @click="changeDiv(3)" id="3"></mt-tab-item>
</mt-navbar>

</span>

<div id="tt"></div>

 <mt-tab-container v-model="active">
  <mt-tab-container-item id="1" class="fisrt">
   <div class="showBlock imgs" style="min-height: 762px; height:100%;" >
	       	   <img v-for="val in data" :src="val.src"/>
   </div> 
  </mt-tab-container-item>
     <mt-tab-container-item id="2" style="min-height:762px; background: #FFFFFF; height:100%;">
      <div class="intro">
         <p> Aptamil 26~10M 800g*4</p>
        <p>  Aptamil</p>
         <p></p>
         <p></p>  
     </div>
  </mt-tab-container-item>
  <mt-tab-container-item id="3" style="min-height:762px; height:100%;">
    <div class="QA">
                <dl>
                    <dt class="Q">1</dt>
                    <dd class="A"></dd>
                </dl>
                <dl>
                    <dt class="Q">2</dt>
                    <dd class="A"></dd>
                </dl>
                <dl>
                    <dt class="Q">3</dt>
                    <dd class="A"></dd>
                </dl>
                <dl>
                    <dt class="Q">4</dt>
                    <dd class="A"></dd>
                </dl>
                <dl>
                    <dt class="Q">5</dt>
                    <dd class="A">243-5</dd>
                </dl>
                <dl>
                    <dt class="Q">6</dt>
                    <dd class="A"></dd>
                </dl>
                <dl>
                    <dt class="Q">7</dt>
                    <dd class="A"></dd>
                </dl>
                <dl>
                    <dt class="Q">8</dt>
                    <dd class="A">
                        <ul>
                            <li>1. 199</li>
                            <li>2. <del>399</del></li>
                            <li>3. /</li>
                        </ul>
                    </dd>
                </dl>
            </div>
  </mt-tab-container-item>
</mt-tab-container>
 
</div>

</template>


<script>
import Vue from 'vue'
import axios from "axios"
Vue.component(Navbar.name, Navbar);
import {Navbar,TabContainer, TabContainerItem } from 'mint-ui';
Vue.component(TabContainer.name, TabContainer);
Vue.component(TabContainerItem.name, TabContainerItem);
const Tab=Vue.extend({
	  name:"tab",
	  data(){
	  	return{
	  	    active:1,
	  		data:null,
	  	}
	  	
	  },
	  mounted(){
	  		var that=this;
			axios.get("./static/json/data.json").then(function(res){
				that.data=res.data
					
			 })
			
		      this.changeDiv(1)	
		      
	  	
	  },
	  methods:{
	  	addCurrent(){
			  var n=$("#tab").offset().top
			   //console.log(n)
			  $(document).scrollTop(n)
			 
			},
		changeDiv(type){

        switch(type){
          case 1: this.active = "1";break;
          case 2: this.active = "2";break;
          case 3: this.active = "3";break;
             }
      }
	  	
	  	
	  }
})

Vue.component("tab",Tab)

export default Tab

	
</script>

<style lang="scss" scoped>
.intro{padding: 0 13px;margin-top: 20px;
       background: #fff !important;
   p{padding:5px 0px;}
  
  }
.QA {
	background: #fff !important;
    padding: 10px 0;
    dl {
    margin: 5px 10px 15px 10px;
    }
     .Q {
    font-weight: 600;
    font-size: 13px;
    color: #343434;
    line-height: 1.3;
   }
     .A {
    margin-top: 8px;
    font-size: 12px;
    color: #5b5b5b;
     }
}
.QA .Q:after, .QA .A:after {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    width: 12px;
    height: 15px;
    background: url(../assets/icon_qa.png) no-repeat;
    -webkit-background-size: 12px auto;
    background-size: 12px auto;
}
.QA .A:after {
    top: 2px;
    background-position: 0 -15px;
}

.QA .Q {
    font-weight: 600;
    font-size: 13px;
    color: #343434;
    line-height: 1.3;
}

.QA .A {
    margin-top: 8px;
    font-size: 12px;
    color: #5b5b5b;
}
.QA .Q, .QA .A{
    position: relative;
    padding-left: 20px;
}
.mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #ff4e88;
    color: #ff4e88;
    margin-bottom: -3px;
    
}
.mint-tab-container-item{background: #ffff !important;}

.titleBar {
    display: flex;
    position: relative;
    margin: 0 auto;
    left: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    background: #fff !important;
    max-width: 640px;
    margin: 0 auto;
    z-index: 3;
    justify-content:space-around;
    margin-top:10px;


    
    }
    #tt{width: 100%;height: 40px;display:none;}

.imgs{
	display: block;
	background: #00AAFF;
	width: 100%;
	height:100%;
	overflow: hidden;
	 img{width:100%;
	    display:block;
	    border: none;
	
	}
	
}
</style>


